html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  /* font-size: 100%; */
  /* font: inherit; */
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  all: unset;
}
a:visited {
  all: unset;
}
a:active {
  all: unset;
}
* {
  box-sizing: border-box;
  overflow: visible;
  word-break: keep-all;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Noto Sans KR", sans-serif;
  transition: all 300ms;
  overflow-x: hidden;
  height: 85vh;
}
@media (min-width: 500px) {
  body {
    height: 100vh;
  }
}

.container {
  width: 80vw;
  /* max-height: 821px; */
  height: fit-content;
  overflow-x: visible;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 375px;
}

.container {
  -ms-overflow-style: none;
}
.container::-webkit-scrollbar {
  display: none;
}

a {
  color: black;
}

